import React, { useEffect, useState } from "react";

export default function Homepage(){

    let [items , setItems] = useState([]);

    useEffect(() => {
        const updateData = setInterval(() => {
            fetch("https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=100&page=1&sparkline=false").then(response => response.json()).then(data => setItems(data))
        }, 500)
        return() => clearInterval(updateData)
    },[])

    return(
        <> 
            <main>
                <table> 
                    <tr className="tableHeader">
                        <th className="rank">Rank</th>
                        <th className="icon">Coin</th>
                        <th className="symbol"></th>
                        <th className="name"></th>
                        <th className="currentPrice">Price</th>
                        <th className="change">Change</th>
                        <th className="high">24H High</th>
                        <th className="low">24H Low</th>
                        <th className="total">Total Volume</th>
                    </tr>
                    {items.map((item) => (
                    <tr key={item.id}>
                        <td className="rank"> {item.market_cap_rank} </td>
                        <td className="icon"> <img src={item.image} alt={item.name} /> </td>
                        <td className="symbol"> {item.symbol} </td>
                        <td className="name"> {item.name} </td>
                        <td className="currentPrice"> ${item.current_price} </td> 
                        {item.price_change_percentage_24h < 0 ? (
                            <td className="change red"> {(item.price_change_percentage_24h).toFixed(2)}% </td>
                        ) : (<td className="change green"> {(item.price_change_percentage_24h).toFixed(2)}% </td>)}
                        <td className="high"> {item.high_24h} </td>
                        <td className="low"> {item.low_24h} </td>
                        <td className="total"> {item.total_volume} </td>
                    </tr>
                    ))}
                </table>
            </main>
        </>
    )}